///// available colors
#specificproductcontainer-availablecolorscontainerlvir {
  margin-top: 10%;
  padding: 10px 180px;
}
#specificproductcontainer-availablecolorsheaderlvir {
  text-align: center;
  color: white;
  font: auto {
    family: $sub-font;
    size: 26px;
  }  
  letter-spacing: 4px;
  color: black;
}
#specificproductcontaineravailablecolor-colorcontainerlvir {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 240px;
}
.specificproductcontaineravailablecolor-colorpositioningcontainerlvir {
  position: relative;
  height: 60%;
  width: 15%;
  border-radius: 50%;
}
.specificproductcontaineravailablecolor-selectedcountlvir {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font: auto {
    family: $main-font;
    size: 60px;
  }
  color: white;
}
.specificproductcontaineravailablecolor-colorpositioningcontaineranimatedlvir {
  position: relative;
  height: 60%;
  width: 15%;
  border-radius: 50%;
  animation: bounce 1.5s linear 2s infinite;
  transition-timing-function: cubic-bezier(.17,.67,.83,.67);
}
@keyframes bounce {
  0%   {bottom:30px;}
  25%  {bottom:20px;}
  50%  {bottom:10px;}
  75%  {bottom:5px;}
  100% {bottom:-10px;}
}
