$mainbgcolor: #16161a;
$subbgcolor:  #27272b;
$containersonidlebgcolor: #e2e4e1;
$main-font: 'Titillium Web', sans-serif;
$sub-font: 'Quicksand', sans-serif;
$robotic-font: 'Nova Round', cursive;
#futuremacholder {
  position: relative;
  background-color: rgb(235, 225, 225);
  width: 100%;
  height: 100vh;
  font: auto {
    family: $main-font;
  }
}
#personalinformation {
  position: relative;
  background-color: $subbgcolor;
  width: 50%;
  height: 20%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 30px;
  top: 5%;
  user-select: none;
  //overflow: hidden;
}
#personalinformation-definetioncontainer {
  position: absolute;
  background-color: #e9bcb7;
  background-image: linear-gradient(315deg, #e9bcb7 0%, #29524a 74%);
  width: 100%;
  height: 100%;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font: auto {
    size: 25px;
  }
}
#personalinformationgreeting {
  letter-spacing: 3px;
  font: auto {
    size: 28px;
  }
}
#contactinformationgreeting {
  letter-spacing: 3px;
  font: auto {
   size: 28px;
  }
}
#addressinformationgreeting {
  letter-spacing: 3px;
  font: auto {
   size: 28px;
  }
}
#personalinformation-formcontainer {
  position: absolute;
  background-color:  rgb(189, 184, 184);
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: top 1s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#personalinformation-contentcontainer {
  display: flex;
}
#personalinformationform {
  //background-color: chocolate;
  width: 45%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#firstname {
  border: none;
  border-radius: 20px;
  height: 7%;
  width: 40%;
  text-align: center;
}
#middlename {
  border: none;
  border-radius: 20px;
  height: 7%;
  width: 40%;
  text-align: center;
}
#lastname {
  border: none;
  border-radius: 20px;
  height: 7%;
  width: 40%;
  text-align: center;
}
#personalinformationform-guidescontainer {
  //background-color: aqua;
  width: 55%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
#personalinformation-exitbuttoncontainer {
  width: 100%;
  height: auto;
}
#personalinformation-exitbutton {
  position: relative;
  background-color: white;
  border: none;
  border-radius: 20px;
  width: 15%;
  padding: 5px 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  top: 100%;
}
#personalinformation-skipbutton {
position:relative;
background-color: white;
border: none;
border-radius: 20px;
width: 15%;
padding: 5px 10px;
display: block;
margin-left: auto;
margin-right: auto;
top: 150%;
}
