$containersonidlebgcolor: #e2e4e1;
#followus {
  background-color: $subbgcolor;
  width: 80%;
  height: 150px;
  color: white;
  padding-left: 10px;
  padding-top: 10px;
  margin-top: 15%;
  margin-left: auto;
  margin-right: auto;
}
#followus-header {
  font-size: 14px;
  letter-spacing: 6.5px;
}
